

import React from 'react';
import './styling/info.scss';

class IndexInfo extends React.Component {
    render() {
        return (
            <div className="indexes-container">
                <h1>General Indexes</h1>
                <h2>
                    The Total Market Cap Crypto Index is the first tokenized, tradable index in the cryptocurrency market. 
                    It is an Adaptive Market Index designed to track the performance and growth of the cryptocurrency market as a whole. 
                    The index continually factors in new cryptocurrencies that arrive to the market to account for the ever-evolving nature 
                    of the cryptocurrency world.
                </h2>
                <br />
                <h1>Adaptive Sector Indexes - Coming Soon</h1>
                <h2>
                    Adaptive Sector Indexes easily track a specific sector of the cryptocurrency market.
                    Adaptive Sector Indexes will be created and managed by the holders of the platform governance token.
                </h2>
            </div>
        )
    }
}

export default IndexInfo;